<template>
    <!--创建服务对话框-->
    <Modal v-model="show">
        <p slot="header">
            <Icon type="information-circled"></Icon>
            <span>{{title}}</span>
        </p>
        <div style="width: 400px">
            <Form class="form" ref="formValidate" :model="formValidate" :rules="ruleValidate" :label-width="120">
                 <FormItem label="事件:" prop="eventName">
                    <Select v-model="formValidate.event" style="width:200px">
                        <Option v-for="item in eventNameList" :value="item.value" :key="item.key">{{ item.label }}</Option>
                    </Select>
                </FormItem> 
                <FormItem label="任务:" prop="taskName">
                    <Select v-model="formValidate.task_id" style="width:200px">
                        <Option v-for="item in taskNameList" :value="item.value" :key="item.key">{{ item.label }}</Option>
                    </Select>
                </FormItem>
            </Form>
        </div>
        <div slot="footer">
            <div style="margin:0 auto;width:70px;">
                <Button type="primary" :loading="loading" @click="submit('formValidate')">创建</Button>
            </div>
        </div>
    </Modal>
</template>


<script>
    import task from '../../../client/task'
    import worker from '../../../client/worker'
    import event from '../../../client/event'

    export default {
        props: {
            title: String,
            taskNameList:Array,
            eventNameList:Array,
        },
        data() {
            return {
                show: false,
                loading: false,
                formValidate: {
                    event: '',
                    task_id: 0,
                },
                ruleValidate: {
                    event: [
                        {required: true, message: '请选择',trigger: 'blur'}
                    ],
                    task_id: [
                        {required: true, message: '请选择', trigger: 'blur'}
                    ],
                },
            }
        },
        methods: {
            open: function () {
                this.show = true;
            },
            submit: function (name) {
                this.$refs[name].validate((valid) => {
                    if (!valid) {
                        return;
                    }

                    let reset = () => {
                        this.formValidate = {};
                        this.loading = false;
                        this.show = false;
                    };
                    let error = (error) => {
                        this.loading = false;
                        this.$Modal.error({
                            title: '提交失败',
                            content: error.response.data.status_msg
                        })
                    };

                    this.loading = true;

                    this.formValidate.task_id = this.formValidate.task_id.toString()
                    event.addTask(this.formValidate, (body) => {
                        this.$emit('on-success', body.data.id);
                        this.$Message.success("创建成功");
                        reset()
                    }, error)
                })
            },
        }
    }
</script>


