import axios from 'axios'
import client from './client'

const URLs = {
    list: 'list',
    delete: 'delete',
    detail: 'detail',
    create: 'create',
    update: 'update',
    push: 'push',
}

let task = {};

var instance = axios.create({
    baseURL: '/api/task',
    timeout: 3000,
});

task.getlist = function (params, success) {
    instance.get(URLs.list, {params: params}).then((response) => {
        if (success !== undefined) {
            success(response.data.data)
        }
    }).catch(client.message);
};

task.deleteTask = function (id, success) {
    instance.put(URLs.delete, {id: id}).then((response) => {
        if (success !== undefined) {
            success(response.data)
        }
    }).catch((e) => {
        if (error !== undefined) {
            error(e)
        } else {
            client.message(e)
        }
    });
};

task.createTask = function (params, success, error) {
    instance.post(URLs.create, params).then((response) => {
        if (success !== undefined) {
            success(response.data)
        }
    }).catch((e) => {
        if (error !== undefined) {
            error(e)
        } else {
            client.message(e)
        }
    });
};

task.updateTask = function (params, success, error) {
    instance.put(URLs.update, params).then((response) => {
        if (success !== undefined) {
            success(response.data)
        }
    }).catch((e) => {
        if (error !== undefined) {
            error(e)
        } else {
            client.message(e)
        }
    });
};

task.pushTask = function (params, success, error) {
    params.args.forEach((arg) => {
        instance.post(URLs.push, {args: JSON.stringify(arg), name: params.name}).then((response) => {
  
        }).catch((e) => {
            if (error !== undefined) {
                error(e)
            } else {
                client.message(e)
            }
        });
    });
    // TODO: This not work, just for now
    if (success !== undefined) {
        success()
    }
};

export default task;



