<style>

</style>

<template>
  <div>
    <div style="font-size:16px; padding: 6px;">事件管理
        <span style="font-size:12px; padding-left:10px">管理系统所有的事件</span>
    </div>
    <div style="height: 50px; ">
        <div style="font-size:16px; padding: 10px; float: left;">
                <Button type="warning" @click="getDefalutList">事 件 列 表</Button>
        </div>
        <div style="padding: 10px; float: right;">
            <ModalRegisterEvent ref="modalRegisterEvent" @on-success="refreshList" title="注册事件"></ModalRegisterEvent>
            <ModalAddTask ref="modalAddTask" @on-success="refreshList" title="新增任务" :task-name-list="taskNameList" :event-name-list="eventNameList"></ModalAddTask>
            <Dropdown>
                <Button type="primary" href="javascript:void(0)">
                    添加
                    <Icon type="arrow-down-b"></Icon>
                </Button>
                <DropdownMenu slot="list">
                    <Button type="text" @click="$refs.modalRegisterEvent.open()">
                        注册事件
                    </Button>
                    <Button type="text" @click="$refs.modalAddTask.open()">
                        添加任务
                    </Button>
                </DropdownMenu>
            </Dropdown>
        </div>
        <div style="float: left;">
            <div style=" padding: 10px;border-bottom: 1px solid #ddd;">
                <div style="left !important;">
                    <Select v-model="serachTask" placeholder ="任务" style="width:200px">
                        <Option v-for="item in taskNameList" :value="item.value" :key="item.key">{{ item.label }}</Option>
                    </Select>
                    <Button type="primary" @click= "getEventByTask">筛选</Button>
                </div>
            </div>
            <Table border :columns="columns" :data="datas"></Table>
        </div>
    </div>
  </div>
</template>

<script>
import task from '../../client/task'
import ModalRegisterEvent from './components/registerEventModal'
import ModalAddTask from './components/addTaskModal'
import event from '../../client/event'

export default {
    components: {
        ModalRegisterEvent,
        ModalAddTask,
    },
    data(){
        return{
            serachTask: "",
            taskNameList:[],
            eventNameList:[],
            columns: [
            {
                title: '序号',
                key: 'id'
            },
            {
                title: '名称',
                key: 'name',
                render: (h, params) => {
                    return h('div', [
                        h('p', {
                            style: {
                                marginRight: '5px',
                                color: 'CornflowerBlue ',
                                cursor: 'pointer'
                            },
                            on: {
                                click: () => {
                                    this.$router.push('/eventManagement/filter?event_name='+ params.row.name)
                                }
                            }
                        },params.row.name)
                    ]);
                }
            },
            {
                title: '描述',
                key: 'intro'
            },
            {
                title: '创建时间',
                key: 'create_time'
            },
            {
                title: '操作',
                key: 'action',
                width: 300,
                align: 'center',
                render: (h, params) => {
                    return h('div', [
                        h('Button', {
                            props: {
                                type: 'primary',
                                size: 'small'
                            },
                            style: {
                                marginRight: '5px'
                            },
                            on: {
                                click: () => {
                                    this.$Modal.confirm({
                                        title: '刷新确认',
                                        content: '您确认进行刷新操作吗？',
                                        onOk: () => {
                                            this.refreshEvent(params.row.id, (body)=>{
                                                this.$Message.success('刷新成功');
                                            })
                                        }
                                    });
                                }
                            }
                        }, '刷新'),
                        h('Button', {
                            props: {
                                type: 'error',
                                size: 'small'
                            },
                            on: {
                                click: () => {
                                    this.$Modal.confirm({
                                        title: '删除确认',
                                        content: '您确认进行删除操作吗？',
                                        onOk: () => {
                                            this.deleteEvent(params.row.id, (body)=>{
                                                this.$Message.success('删除成功');
                                            })
                                        }
                                    });
                                }
                            }
                        }, '删除')
                    ]);
                }
            }
          ],
          datas: [],
        }
    },
    methods: {
        refreshList: function(){
            event.getlist({}, (body) => {
                if (this.serachTask === "") {
                    if (body.data.list!=null) {
                        this.datas = body.data.list;
                    } else {
                        this.datas = []
                    }
                } else {
                    event.getEventByTask({task_id: this.serachTask},(body) => {
                        if (body.data.list!=null) {
                            this.datas = body.data.list;
                        } else {
                            this.datas = []
                        }
                    })
                }
                this.datas.forEach((value,index) =>{
                    this.eventNameList.push({value: value.name, label: value.name})
            　　 });
            })
            task.getlist({topic: '', page_no: 1, page_size: 1000}, (body) => {
                if (body.data.list != null) {
                    body.data.list.forEach((value,index) =>{
                        this.taskNameList.push({value: value.id, label: value.name})
    　　             });
                }
            })
        },
        deleteEvent(id) {
            event.deleteEvent(id, () => {
                this.refreshList()
            })
        },
        refreshEvent() {
            event.refreshEvent(() => {
                this.refreshList()
            })
        },
        getEventByTask() {
            if (this.serachTask !== "") {
                event.getEventByTask({task_id: this.serachTask},(body) => {
                    if (body.data.list!=null) {
                        this.datas = body.data.list;
                    } else {
                        this.datas = []
                    }
                })
            }
        },
        getDefalutList() {
            this.serachTask = ""
            this.refreshList()
        }
    },
    mounted: function () {
        this.serachTask =  ""
        event.getlist({}, (body) => {
            if (body.data.list!=null) {
                this.datas = body.data.list;
            } else {
                this.datas = []
            }
            this.datas.forEach((value,index) =>{
                this.eventNameList.push({value: value.name, label: value.name})
        　　 });
        })
        task.getlist({topic: '', page_no: 1, page_size: 1000}, (body) => {
            if (body.data.list != null) {
                body.data.list.forEach((value,index) =>{
                    this.taskNameList.push({value: value.id, label: value.name})
　　             });
            }
        })
    }
}
</script>



